$arrow:'../fonts/arrow';

// font family Inter
@font-face {
  font-family: 'Inter';
  src: url('#{$inter}/Inter-Black.eot'); //IE9 Compat Modes
  src: url('#{$inter}/Inter-Black.eot?#iefix') format('embedded-opentype'), // IE6-IE8 /
  url('#{$inter}/Inter-Black.woff2') format('woff2'), // Super Modern Browsers /
  url('#{$inter}/Inter-Black.woff') format('woff'), // Pretty Modern Browsers /
  url('#{$inter}/Inter-Black.ttf')  format('truetype'), // Safari, Android, iOS /
  url('#{$inter}/Inter-Black.svg#svgFontName') format('svg'); // Legacy iOS /
  font-weight: 900;
  font-style: normal;
}
