@font-face {
  font-family: 'icomoon';
  src:  url('#{$arrow}/icomoon.eot?jnvkam');
  src:  url('#{$arrow}/icomoon.eot?jnvkam#iefix') format('embedded-opentype'),
  url('#{$arrow}/icomoon.ttf?jnvkam') format('truetype'),
  url('#{$arrow}/icomoon.woff?jnvkam') format('woff'),
  url('#{$arrow}/icomoon.svg?jnvkam#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  //speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-long-arrow-right:before {
  content: "\f178";
}
